	module.exports = {"data":{"markdownRemark":{"html":"<ol>\n<li>All code is written and pushed to a branch on the developers’ own fork of Free Code Camp.</li>\n<li>Developers create pull requests, pulling code from their branch to Free Code Camp’s staging branch.</li>\n<li>Someone other than the person who submitted the pull request QA’s the pull request and accepts it if everything is perfect. Otherwise they close the pull request with an explanation of why it isn’t perfect, and the developer opens another pull request once they’ve fixed things.</li>\n<li>Master is deployed to freecodecamp.com periodically.</li>\n</ol>\n<h2>Notes:</h2>\n<ul>\n<li>This is subject to change once we get better test coverage and continuous integration working.</li>\n</ul>","fields":{"slug":"/meta/free-code-camp-deployment-workflow/"},"frontmatter":{"title":"Free Code Camp Deployment Workflow"}}},"pathContext":{"slug":"/meta/free-code-camp-deployment-workflow/"}}

